<template>
  <div class="project">
    <div class="space_top">
      <div @click="reloadPage()">
        <img
          src="../../assets/swt_img/logo_n.png"
          alt=""
          class="space_top_logo"
        />
      </div>
      <div class="space_top_right">
        <img
          src="../../assets/swt_img/right_menu.png"
          alt=""
          class="space_top_right_img2"
          @click="showMean()"
        />
      </div>
    </div>
    <div class="space_back">
      <div class="space_home_trade">
        <div>
          <span class="project_title">项目介绍</span>
          <p class="project_detail">
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;桑植白茶数字藏品(SWT),发行总量5000W,互转可绑定关系,通过购买茶叶获得SWT,获得的SWT持有可获得收益增值分红。
            SWT可兑换茶叶,后期可购买专属茶叶NFT。
          </p>
        </div>
        <div class="project_mid">
          <img
            class="project_img3"
            src="../../assets/swt_img/project_img3.png"
            alt=""
          />
          <p class="project_content" @click="toToken">
            合约: 0x51d510a4e9529AA4AfC00bd2D77D5A31925E8A58
          </p>
        </div>

        <div class="project_white">
          <div class="project_now">
            <div class="project_now_left">
              <div class="project_now_title">实时行情</div>
              <div class="project_now_t">
                <p>现货</p>
                <p>最新价</p>
                <!-- <p>24H涨幅</p> -->
              </div>
              <div class="project_now_l1">
                <p>SWT</p>
                <p v-if="priceShow">${{ swtPrice || "0.1000" }}</p>
                <!-- <p>+11.2%</p> -->
              </div>
              <div class="project_now_l2">
                <p>ETH</p>
                <p v-if="priceShow">
                  ${{ parseFloat(ETHModel.price).toFixed(4) || "0.0000" }}
                </p>
                <!-- <p>$12.5</p> -->
              </div>
              <div class="project_now_l1">
                <p>EOS</p>
                <p v-if="priceShow">
                  ${{ parseFloat(EOSModel.price).toFixed(4) || "0.0000" }}
                </p>
                <!-- <p>+11.2%</p> -->
              </div>
              <div class="project_now_l2">
                <p>BNB</p>
                <p v-if="priceShow">
                  ${{ parseFloat(BNBModel.price).toFixed(4) || "0.0000" }}
                </p>
                <!-- <p>+11.2%</p> -->
              </div>
              <div class="project_now_l1">
                <p>XRP</p>
                <p v-if="priceShow">
                  ${{ parseFloat(XRPModel.price).toFixed(4) || "0.0000" }}
                </p>
                <!-- <p>+11.2%</p> -->
              </div>
            </div>
            <div class="project_now_right">
              <img src="../../assets/swt_img/project_img1.png" alt="" />
            </div>
          </div>
        </div>

        <div>
          <span class="project_title">交易规则</span>
          <div class="project_rule">
            <div class="project_rule_left">
              <p>1.交易滑点,买卖各3%</p>
              <p>2.买卖滑点通缩进入黑洞</p>
              <p>3.通缩至1000W</p>
            </div>
            <div class="project_rule_right">
              <img
                @click="toPancakeswap"
                src="../../assets/swt_img/project_img2.png"
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    <template v-if="meanShow">
      <div class="index_pop_content">
        <div class="index_pop_top">
          <div class="index_pop_top_left">
            <img src="../../assets/swt_img/logo_w.png" alt="" />
          </div>
          <div>
            <img
              src="../../assets/swt_img/tea_close.png"
              alt=""
              class="space_mean_img"
              @click="showMean()"
            />
          </div>
        </div>
        <div class="index_pop_main_tab" @click="goback()">
          <div class="index_pop_middle">
            <a href="javascript:;">
              <div class="index_pop_middle_left">
                <span>首页</span>
              </div>
              <div class="index_pop_middle_right"></div>
              <div class="index_clear"></div>
            </a>
          </div>
        </div>
        <div class="index_pop_main_tab" @click="meanShow = false">
          <div class="index_pop_middle">
            <a href="javascript:;">
              <div class="index_pop_middle_left">
                <span>项目介绍</span>
              </div>
              <div class="index_pop_middle_right"></div>
              <div class="index_clear"></div>
            </a>
          </div>
        </div>
        <div class="index_pop_main_tab" @click="goback()">
          <div class="index_pop_middle">
            <a href="javascript:;">
              <div class="index_pop_middle_left">
                <span>产品介绍</span>
              </div>
              <div class="index_pop_middle_right"></div>
              <div class="index_clear"></div>
            </a>
          </div>
        </div>
        <div class="index_pop_main_tab" @click="goback()">
          <div class="index_pop_middle">
            <a href="javascript:;">
              <div class="index_pop_middle_left">
                <span>产品相册</span>
              </div>
              <div class="index_pop_middle_right"></div>
              <div class="index_clear"></div>
            </a>
          </div>
        </div>
        <div class="index_pop_main_tab" @click="goback()">
          <div class="index_pop_middle">
            <a href="javascript:;">
              <div class="index_pop_middle_left">
                <span>NFT</span>
              </div>
              <div class="index_pop_middle_right"></div>
              <div class="index_clear"></div>
            </a>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>
<script>
import { WOW } from "wowjs";
import Swiper from "swiper";
import {
  apiRegister,
  apiGetArtList,
  apiRecommendList,
  apiBannerList,
  apiGetConfig,
} from "@/request/api";

let line;
let provider, signer, defaultAccount, networkVersion;
let conContract;
let timeStart;
export default {
  data() {
    return {
      meanShow: false,
      connect_show: false,
      langShow: false,
      bannerList: [],
      ETHModel: {},
      EOSModel: {},
      BNBModel: {},
      XRPModel: {},
      // SWTModel: {},
      // ethPrice: "",
      // eosPrice: "",
      // bnbPrice: "",
      // xrpModel:"",
      swtPrice: "0.1000",
      priceShow: false,
    };
  },
  created: function () {
    // this.getBannerList();
    this.getETHprice();
    this.getEOSprice();
    this.getBNBprice();
    this.getXRPprice();
    this.postSWTprice();
  },
  beforeDestroy() {
    if (timeStart) {
      //如果定时器还在运行 或者直接关闭，不用判断
      clearInterval(timeStart); //关闭
    }
  },
  async mounted() {
    var wow = new WOW({
      boxClass: "wow", // animated element css class (default is wow)
      animateClass: "animated", // animation css class (default is animated)
      offset: 0, // distance to the element when triggering the animation (default is 0)
      mobile: true, // trigger animations on mobile devices (default is true)
      live: true, // act on asynchronously loaded content (default is true)
      callback: function (box) {
        // the callback is fired every time an animation is started
        // the argument that is passed in is the DOM node being animated
      },
      scrollContainer: null, // optional scroll container selector, otherwise use window
    });
    wow.init();
    new Swiper(".banner-swiper", {
      autoplay: true, //可选选项，自动滑动
      loop: true,
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
      lazy: {
        loadPrevNext: true,
      },
      observer: true,
      observeParents: true,
    });
    var _this = this;
    timeStart = setInterval(function () {
      _this.getETHprice();
      _this.getEOSprice();
      _this.getBNBprice();
      _this.getXRPprice();
      _this.postSWTprice();
    }, 10000);
  },
  methods: {
    toHome() {
      this.$router.push({
        name: "home",
        query: {
          // p_address: this.p_address,
        },
      });
    },
    goback() {
      this.$router.replace({
        name: "home",
        query: {},
      });
    },
    toNftTrade() {
      this.$router.push({
        name: "project",
        query: {
          // p_address: this.p_address,
        },
      });
    },
    reloadPage() {
      location.reload();
    },
    //显示菜单
    showMean() {
      if (this.meanShow) {
        this.meanShow = false;
      } else {
        this.meanShow = true;
      }
    },
    getBannerList() {
      var _this = this;
      // 用户注册
      apiBannerList({}).then((res) => {
        _this.bannerList = res.data;
      });
    },
    getETHprice() {
      let _this = this;
      this.priceShow = false;
      $.get(
        this.api.ethUrl,
        {},
        function (res) {
          if (res.code == 200) {
            _this.priceShow = true;
            _this.ETHModel = res.data.markets[0];
          }
        },
        "JSON"
      );
    },
    getEOSprice() {
      let _this = this;
      this.priceShow = false;
      $.get(
        this.api.eosUrl,
        {},
        function (res) {
          if (res.code == 200) {
            _this.priceShow = true;
            _this.EOSModel = res.data.markets[0];
          }
        },
        "JSON"
      );
    },
    getBNBprice() {
      let _this = this;
      this.priceShow = false;
      $.get(
        this.api.bnbUrl,
        {},
        function (res) {
          if (res.code == 200) {
            _this.priceShow = true;
            _this.BNBModel = res.data.markets[0];
          }
        },
        "JSON"
      );
    },
    getXRPprice() {
      let _this = this;
      this.priceShow = false;
      $.get(
        this.api.xrpUrl,
        {},
        function (res) {
          if (res.code == 200) {
            _this.priceShow = true;
            _this.XRPModel = res.data.markets[0];
          }
        },
        "JSON"
      );
    },
    postSWTprice() {
      let _this = this;
      this.priceShow = false;
      $.ajax({
        url: this.api.swtUrl,
        data: JSON.stringify({
          query:
            '\n      query pools {\n        now: pairs(\n    where: { id_in: ["undefined","0x125af71165a96542c8954d86685b0fc03be665d3"] }\n    \n    orderBy: trackedReserveBNB\n    orderDirection: desc\n  ) {\n    id\n    reserve0\n    reserve1\n    reserveUSD\n    volumeUSD\n    token0Price\n    token1Price\n    token0 {\n      id\n      symbol\n      name\n    }\n    token1 {\n      id\n      symbol\n      name\n    }\n  }\n    }\n    ',
          operationName: "pools",
        }),
        type: "POST",
        processData: false,
        contentType: "application/json", //typically 'application/x-www-form-urlencoded', but the service you are calling may expect 'text/json'... check with the service to see what they expect as content-type in the HTTP header.
        success: function (res) {
          _this.priceShow = true;
          let SWTModel = res.data.now[0];
          _this.swtPrice = parseFloat(SWTModel.token1Price).toFixed(4);
          if (_this.swtPrice == NaN || _this.swtPrice == null) {
            _this.swtPrice = "0.1000";
          }
          // console.log(_this.SWTModel.token1Price);
        },
      });
    },
    toToken() {
      window.open(
        "https://bscscan.com/address/" + this.api.swtAddress,
        "_self"
      );
    },
    toPancakeswap() {
      window.open(
        "https://pancakeswap.finance/swap?outputCurrency=" +
          this.api.swtAddress +
          "&inputCurrency=0x55d398326f99059ff775485246999027b3197955&utm_source=tokenpocket",
        "_self"
      );
    },
  },
};
</script>


